.fm-container {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: stretch;
    align-content: stretch;
    @include default-font(12px);
    background-color: $color-container-bg;
}
